<template>
    <div class="display">
       <section class="wall-display">
           <div>
               <h1>NITDA REPORTS</h1>
           </div>
        </section> 

        <section class="right-section">
            <div class="link-wrapper">
                <div class="logo">
                    <img src="../../../../assets/nitda_logo.png" alt="" width="200">
                </div>

                <div class="login-form">
                    <div class="error" v-if="$store.state.auth.errorMsg" @click="clearErr">
                        <span>{{ $store.state.auth.errorMsg }}</span>
                        <i class="fa fa-times" ></i>
                    </div>
                    <div>
                        <label for="email">Email</label>
                        <div>
                            <input type="email" name="email" id="email" placeholder="Enter your email..." v-model="formData.email"> 
                        </div>
                    </div>
                    <div>
                        <label for="password">Password</label>
                        <div>
                            <input type="password" name="password" id="password" placeholder="Enter your password..." v-model="formData.password">
                        </div>
                    </div>
                    <div>
                        <div>
                            <button class="btn" id="button" @click="loginUser">{{ loginStatus }}</button>
                        </div>
                    </div>
                </div>

                <footer>
                    <div class="footer-text">
                        Copyright &copy; 2021, NITDA
                    </div>
                </footer>
            </div>
        </section>
    </div>
</template>

<script>
export default {
    data(){
        return {
            formData: {
                email: "",
                password: ""
            },
            loginStatus: "Login"
        }
    },
    methods: {
        loginUser() {
            const formData = {
                email: this.formData.email.toLowerCase(),
                password: this.formData.password
            }

            this.$store.dispatch('login', formData)
            this.loginStatus = "Loading..."
            setTimeout(() => {
                if(this.$store.state.auth.errorMsg) {
                    this.loginStatus = "Login"
                }
            }, 200)
        },
        clearErr(){
            this.$store.dispatch("clearError")
        }
    }
}
</script>

<style scoped>
    .display {
        display: flex;
    }

    .wall-display {
        flex: 4;
        background: linear-gradient(rgba(0,0,0,0.8), rgba(0,0,0,0.7)), url('https://images.pexels.com/photos/5699456/pexels-photo-5699456.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260');
        background-size: cover;
        color: #fff;
        background-attachment: fixed;
    }

    .wall-display h1 {
        padding-top: 3.5em;
        margin: 0 auto;
        width: 50%;
        font-size: 4rem;
        text-align: center;
    }

    .right-section {
        flex: 2.3;
    }

    .link-wrapper {
        width: 80%;
        margin: 10% auto;
    }

    .logo {
        text-align: center;
        margin-bottom: 5rem;
    }

 

    .login-form {
        margin-bottom: 40%;
    }

    .login-form input {
        width: 100%;
        padding: 13px;
        font-size: 16px;
        outline-color: #000;
        margin-top: 5px;
        border: 1px solid  #ccc;
        border-radius: 5px;
        font-family: inherit;
    }

    .login-form > div {
        margin-bottom: 30px;
    }

    .login-form .btn {
        padding: 10px;
        font-weight: bold;
        font-family: inherit;
        width: 100%;
        cursor: pointer;
        font-size: 16px;
        border: none;
        background-color: #004E1F;
        color: #fff;
        border-radius: 5px;
    }

    .login-form .btn:hover {
        background-color: #04973f;
        transition: 0.3s;
    }

    label {
        font-weight: bold;
    }

    footer {
        text-align: center;
        /* margin-top: 50px; */
        padding-bottom: 10px;
    }

    .fa-users, .fa-user {
        margin-right: 8px;
    }

    .btn-mic {
        background-color: rgb(18, 57, 95);
        padding: 10px;
        font-weight: bold;
        font-family: inherit;
        width: 100%;
        cursor: pointer;
        font-size: 16px;
        border: none;
        color: #fff;
        border-radius: 5px;
    }

    .or {
        text-align: center;
    }

    .error {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: red;
    }

    .error .fa-times {
        cursor: pointer;
        font-size: 14px;

    }

    .error .fa-times:hover {
        color: #ffacac;
    }

    @media(max-width: 769px) {
        .wall-display {
            display: none;
        }
    }

    @media(min-height: 750px) {
        .logo {
            margin-bottom: 10rem;
        }

        .logo img {
            width: 250px;
        }
    }
</style>