<template>
    <div>
        <Header v-on:toggle-bar="toggleBar"/>
        <div class="wrapper">
        <div class="overview">
            <div>
                <h3>Approved Reports</h3>
                <div class="result">
                    <div class="txt">{{ approved.length }}</div>
                </div>
            </div>

            <div>
                <h3>Unapproved Reports</h3>
                <div class="result">
                     <div class="txt">{{ unapproved.length }}</div>
                </div>
            </div>

            <div>
                <h3>Total Reports</h3>
                <div class="result">
                     <div class="txt">{{ reports.length }}</div>
                </div>
            </div>
        </div>

        <div class="map">
            <Map/>
        </div>  
    </div>
    </div>
</template>

<script>
import Header from '../../../components/Header.vue'
import Map from './components/Map.vue'
import axios from 'axios'

export default {
    components: {
        Header,
        Map
    },
    data(){
        return {
            reports: [],
            approved: [],
            unapproved: []
        }
    },
    methods: {
        toggleBar(){
            this.$emit("toggle-side")
        }
    },
    created(){
        axios.get(`http://lit-shore-55440.herokuapp.com/api/reports`)
        .then(res => this.reports = res.data.reports)
        .catch(err => this.errors.push(err))

        setTimeout(() => {
            this.approved = this.reports.filter(report => report.approved === true)
            this.unapproved = this.reports.filter(report => report.approved === false)
        }, 1000)
    }
}
</script>

<style scoped>
    .wrapper {
        margin: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        font-size: 15px;
    }

    .overview {
        display: flex;
        justify-content: space-between;
        text-align: center;
    }

    .overview > div {
        padding: 20px;
        background: #fff;
        margin-bottom: 20px;
        width: 100%;
    }

    .overview > div h3 {
        margin-bottom: 20px;
    }

     .result .txt {
        font-size: 2rem;
        color: green
    }

    .result {
        padding: 20px;
        background-color: #f3f3f3;
    }

    .map {
        border: 1px solid #ccc;
        height: 350px;
    }

    .fa-check {
        color: green;
    }

    .fa-times {
        color: red
    }

    @media(min-width: 768px) {
        .overview > div {
            margin-right: 10px;
        }
    }

    @media(max-width: 768px) {
        .overview {
            flex-direction: column;
            text-align: center;
        }

        .extras {
            flex-direction: column;
        }

        .extras > div {
            margin-bottom: 20px;
        }

        .wrapper {
            font-size: 14px;;
        }
    }
</style>