                    <div class="report-item" v-for="(report, index) in user.reports" :key="report._id" v-show="index < 5">
                        <span>
                            <router-link to="/report/1">{{ report.projectData.projectTitle }}</router-link>
                        </span>
                        <div>
                            <i class="fa fa-check" v-if="report.approved"></i>
                            <i class="fa fa-minus" v-if="!report.approved"></i>
                        </div>
                    </div>
                </div>
            </div>
            

