    </div>
    </div>
</template>

<script>
import Header from '../../../components/Header.vue'

export default {
    components: {
        Header
    },
    computed: {
        user() {
            return this.$store.getters.user
        }
    },
    data(){
        return {
            reviewed: [],
            unreviewed: []
        }
    },
    created(){
        this.reviewed = this.user.reports.filter(report => report.approved === true)
        this.unreviewed = this.user.reports.filter(report => report.approved === false)
    },
    methods: {
        toggleBar(){
            this.$emit("toggle-side")   
        }
    }
}
</script>

