                <i class="fa fa-search"></i>
                <input type="text" placeholder="Search reports" ref="search">
            </div>
            <div class="filters">
                <div class="sort">Sort by 
                    <select>
                        <option value="date">Upload Date</option>
                        <option value="name">Project Title(A -  Z)</option>
                    </select>
                </div>

                <div class="add-spot" v-if="!user.isAdmin">
                    <button class="btn-add" @click="$router.push('/report/new')"><span class="btn-text">New Report</span> <i class="fa fa-plus"></i></button>
                </div>
            </div>
        </div>

        <div class="reports">
            <div class="report-item" v-for="(report) in user.reports" :key="report.id">
