                    <span class="small">{{ report.projectData.projectTitle.substring(0, 50) }}<span v-if="report.projectData.projectTitle.length > 50">...</span></span>
                    <span class="date">
                        {{ new Date(report.createdAt).getDate() }}/{{ new Date(report.createdAt).getMonth() + 1 }}/{{ new Date(report.createdAt).getFullYear() }}
                    </span>
                </router-link>
                <div class="del-btn">
                    <i class="fa fa-trash"></i>
                </div>          
            </div>
        </div>

        <!-- <router-link class="more-link" to="#">More Reports</router-link> -->
    </div>
</template>

<script>
export default {
    computed: {
        user(){
