import LandingPage from './routes/landing_page/LandingPage'
import Login from './routes/auth/login/Login'
import Signup from './routes/auth/signup/Signup'
import UserPage from './routes/user/UserPage'
import Dashboard from './routes/user/dashboard/Dashboard'
import Reports from './routes/user/reports/Report'
import NewReport from './routes/user/reports/new_report/NewReport'
import ReportView from './routes/user/reports/view/ReportView'
import Settings from './routes/user/settings/Settings'
import Feedback from './routes/user/feedback/Feedback'

//Admin Page Routes
import AdminPage from './routes/admin/AdminPage'
import adminDashboard from './routes/admin/dashboard/Dashboard'
import Requests from './routes/admin/requests/Requests'
import AdminReports from './routes/admin/reports/Report'
import AdminReportView from './routes/admin/reports/view/ReportView'

import VueRouter from 'vue-router'
import store from './store/store'

const routes = [
    { path: '',  component: LandingPage },
    { path: '/login',  component: Login, beforeEnter(to, from, next){
        setTimeout(() => {
            if(store.state.auth.token){
                next('/dashboard')
            } else {
                next()
            }
        }, 200);
    } },
    { path: '/signup',  component: Signup },
    { path: '/report/new', component: NewReport },
    { path: '/dashboard',  component: UserPage, children: [
        { path: "", component: Dashboard },
        { path: "/reports", component: Reports },
        { path: "/reports/:id", component: ReportView },
        { path: "/settings", component: Settings },
        { path: "/feedback", component: Feedback },
    ], beforeEnter(to, from, next){
        setTimeout(() => {
            if(store.state.auth.token){
                next()
            } else {
                next('/login')
            }
        }, 200);
    } },
    { path: '/admin', component: AdminPage, children: [
        { path: "", component: adminDashboard },
        { path: "/request", component: Requests },
        { path: "/report", component: AdminReports },
        { path: "/report/:id", component: AdminReportView }
    ], beforeEnter(to, from, next){
        setTimeout(() => {
            if(store.state.auth.token){
                next()
            } else {
                next('/login')
            }
        }, 200);
    }  }
]

export default new VueRouter({
    routes,
    mode: 'history'
  });