<template>
    <div class="display">
       <section class="wall-display">
           <div>
               <h1>NITDA REPORTS</h1>
           </div>
        </section> 

        <section class="right-section">
            <div class="link-wrapper">
                <div class="logo">
                    <img src="../../../../assets/nitda_logo.png" alt="" width="200">
                </div>

                <form class="login-form"  @submit.prevent="request" autocomplete="off">
                    <div v-if="msg" class="msg">
                        {{ msg }} <router-link to="/login">Login</router-link>   
                    </div>
                    <div v-if="error" class="error">
                        {{ error }}
                    </div>
                    <div>
                        <label for="email">Email</label>
                        <div>
                            <input type="email"  id="email" placeholder="Enter your email..." v-model="email" required>
                        </div>
                    </div>
                    <div>
                        <label for="phone">Phone Number</label>
                        <div>
                            <input type="phone" placeholder="Enter your phone number..." v-model="phone" required>
                        </div>
                    </div>
                    <div>
                        <label for="password">Password</label>
                        <div>
                            <input type="password" placeholder="Enter your Password..." v-model="password" required>
                        </div>
                    </div>
                    <div>
                        <div>
                            <button class="btn" id="button">Submit Request</button>
                        </div>
                    </div>
                </form>

                <footer>
                    <div class="footer-text">
                        Copyright &copy; 2021, NITDA
                    </div>
                </footer>
            </div>
        </section>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data(){
        return {
            email: "",
            phone: "",
            password: "",
            msg: "",
            error: ""
        }
    },
    methods: {
        request() {
            axios.post('http://lit-shore-55440.herokuapp.com/api/auth/signup', {
                email: this.email.toLowerCase(),
                phone: this.phone,
                password: this.password
            })
            .then(res => {
                if(res.data.success) {
                    this.msg = "Signup Successfull. Please await approval from Admin or "
                    this.email = ""
                    this.phone = ""
                    this.password = ""
                }
            })
            .catch(err => this.error = err)
        }
    }
}
</script>

<style scoped>
    .display {
        display: flex;
        height: 100vh;
    }

    .wall-display {
        flex: 4;
        background: linear-gradient(rgba(0,0,0,0.8), rgba(0,0,0,0.7)), url('https://images.pexels.com/photos/5699456/pexels-photo-5699456.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260');
        background-size: cover;
        color: #fff
    }

    .wall-display h1 {
        padding-top: 3.5em;
        margin: 0 auto;
        width: 50%;
        font-size: 4rem;
        text-align: center;
    }

    .right-section {
        flex: 2.3;
    }

    .link-wrapper {
        width: 80%;
        margin: 10% auto;
    }

    .logo {
        text-align: center;
        margin-bottom: 5rem;
    }

 

    .login-form {
        margin-bottom: 30%;
    }

    .login-form input {
        width: 100%;
        padding: 13px;
        font-size: 16px;
        outline-color: #000;
        margin-top: 5px;
        border: 1px solid  #ccc;
        border-radius: 5px;
        font-family: inherit;
    }

    .login-form > div {
        margin-bottom: 30px;
    }

    .login-form .btn {
        padding: 10px;
        font-weight: bold;
        font-family: inherit;
        width: 100%;
        cursor: pointer;
        font-size: 16px;
        border: none;
        background-color: #004E1F;
        color: #fff;
        border-radius: 5px;
    }

    .login-form .btn:hover {
        background-color: #04973f;
        transition: 0.3s;
    }

    label {
        font-weight: bold;
    }

    footer {
        text-align: center;
        padding-bottom: 10px;
    }

    .fa-users, .fa-user {
        margin-right: 8px;
    }

    .btn-mic {
        background-color: rgb(18, 57, 95);
        padding: 10px;
        font-weight: bold;
        font-family: inherit;
        width: 100%;
        cursor: pointer;
        font-size: 16px;
        border: none;
        color: #fff;
        border-radius: 5px;
    }

    .or {
        text-align: center;
    }

    .msg {
        color: green;
        font-weight: bold;
    }

    .error {
        color: red;
        font-weight: bold;
    }

    @media(max-width: 769px) {
        .wall-display {
            display: none;
        }
    }

    @media(min-height: 750px) {
        .logo {
            margin-bottom: 10rem;
        }

        .logo img {
            width: 250px;
        }
    }
</style>