<template>
  <div>
    <GmapMap
  :center="{lat:lat, lng:lng}"
  :zoom="15"
  map-type-id="terrain"
  style="width: 100%; height: 350px"
>
  <GmapMarker
    v-for="(m, index) in markers"
    :key="index"
    :position="m.position"
    :clickable="true"
    :draggable="true"
    @click="center=m.position"
  />
</GmapMap>
  </div>
</template>

<script>
import axios from'axios'

export default {
  data(){
    return {
      lat: '',
      lng: '',
      markers: [],
      reports: ''
    }
  },
  computed: {
    user(){
      return this.$store.getters.user
    }
  },
  created() {
    if(navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(position => {
         this.lng = position.coords.longitude
         this.lat = position.coords.latitude
        //  setTimeout(() => {
        //    this.markers = [
        //    {
        //      position: { lat: this.lat, lng: this.lng }
        //    },
        //  ]
        //  }, 2000)
         this.$emit('get-coords', {lng: position.coords.longitude,  lat: position.coords.latitude})
        }, error => {
          console.log(error.message)
        } )
      }

      axios.get(`http://lit-shore-55440.herokuapp.com/api/reports`)
      .then(res => {
        res.data.reports.forEach((report) => {
          this.markers.push({
            position: report.projectData.gpsCoords
          })
        })
      })
      .catch(err => this.errors.push(err))
  }
}
</script>