          q10: "",
          q11: "",
          q12: "",
        }
      }
    }
  },
  components: {
    Map
  },
  computed: {
    user(){
      return this.$store.getters.user
    }
  },
  methods: {
    getCoords({lng, lat}) {
      this.formData.gpsCoords.lng = lng,
      this.formData.gpsCoords.lat = lat
    },
    nextSection() {
      this.page = 2
    },
    submitReport(){
      axios.post('http://lit-shore-55440.herokuapp.com/api/report/add', {
        projectData: this.formData,
        user: this.user._id
      })
      .then(res => {
        console.log(res.data)
      })
      .catch(err => {
        console.log(err)
      })
      //projectData
    }
  }
}
