import axios from 'axios'
import router from '../../router'

const state = {
    token: null,
    user: {
        _id: '',
        email: '',
        phone: '',
        reports: [],
        isAdmin: null
    },
    errorMsg: null,
}

const mutations = {
    authUser(state, token){
        state.token = token.token
    },
    storeUser(state, userData){
        state.user = userData.userData
    },
    logoutUser(state){
        state.token = null,
        state.errorMsg = null
        localStorage.removeItem('token')
        localStorage.removeItem("user")
    },
    clearAuthError(state){
        state.errorMsg = null
    }
}

const actions = {
    login({commit, state}, authData){
        axios.post('http://lit-shore-55440.herokuapp.com/api/auth/login', authData)
        .then((res) => {
            commit('authUser', {
                token: res.data.token
            })
            localStorage.setItem("token", res.data.token)
            if(res.data.isAdmin) {
                router.replace(`/admin`)
            } else {
                router.replace(`/dashboard`)
            }
        })
        .catch(err => {
            state.errorMsg = err
            console.log(err.response.data.msg)
            setTimeout(() => {
                state.errorMsg = ""
            }, 4000);
        })
    },
    tryAutoLogin({commit}){
        const token = localStorage.getItem("token");
        commit('authUser', {
            token: token
        })
    },
    fetchUser({commit, state}){
        if(!state.token){
            return
        }
        axios.get('http://lit-shore-55440.herokuapp.com/api/user', {
            headers: {
                token: state.token
            }
        })
        .then(res => {
            commit('storeUser', {
                userData: res.data.user
            })
            const user = JSON.stringify(res.data.user)
            localStorage.setItem('user', user)

        })
        .catch(err => state.errorMsg = err.response.data.error)
    },
    logout({commit}){
        commit('logoutUser')
        router.replace('/login')
    },
    clearError({commit}){
        commit("clearAuthError")
    }
}

const getters = {
    isAuthenticated(state){
        return state.token !== null;
    },
    user(state){
        return state.user;
    },
}

export default {
    state,
    mutations,
    actions,
    getters
}