<template>
    <div class="report-view">
        <Header/>
        <div class="report-info">
            <div class="msg" v-if="msg">{{ msg }}</div>
            <div class="top">
                <button class="btn btn-approve" v-if="!answer.approved" @click="approveReport">Approve Report</button>
                <button class="btn btn-disapprove" v-else @click="rejectReport">Disapprove Report</button>
            </div>
            <div class="info" v-for="(value, name, index) in answer.projectData" :key="index" v-show="index !== 16">
                <div>
                    <div class="label">{{ questions[index] }}</div>
                    <div class="value"><span v-show="index !== 7">{{ value }}</span> <span v-show="index == 7"><b>Longitude:</b> {{ value.lng }}, <b>Latitude:</b> {{ value.lat }}</span></div>
                </div>
            </div>
            <div class="info second-section" v-for="(value, name, index) in secondSection" :key="index">
                <div>
                    <div class="label">{{ secondQuestion[index] }}</div>
                    <div><span class="available">Available:</span> {{ value }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Header from '../../../../components/Header.vue'

export default {
    components: {
        Header
    },
    data(){
        return {
            msg: "",
            questions: [
                "Project Title", "Project Location", "Name of eLearning Solution", "Name of Company", "Company Phone",
                "Company Email", "Date of Verication", "GPS Coordinates", "Centre Contact Person 1 Name", "Centre Contact Person 1 Phone", "Centre Contact Person 1 Email",
                "Centre Contact Person 2 Name", "Centre Contact Person 2 Phone","Centre Contact Person 2 Email", "Name of Verifier One",
                "Name of Verifier Two"
            ],
            answer: {

            },
            secondQuestion: [
                "SERVER - INTEL XEON (QUAD CORE, 3.0GHZ), 8GB RAM, 1TB HARD DISK DRIVE, KEYBOARD, MOUSE, DVD DRIVE (OPTIONAL), MONITOR (OPTIONAL),",
                "GENUINE WINDOWS SERVER 2012 OR HIGHER, INSTALLATION CD (MANDATORY), KEY (MANDATORY),", "AVG Antivirus (ONE YEAR SUBSCRIPTION)",
                'ELECTRIC Projector SCREEN WITH REMOTE CONTROL: 96" x 96", 120 inches diagonal size, bright white screen', "3200 Lumens Projector WITH HDMI AND VGA PORTS",
                "PROJECTOR Ceiling mount (quality aluminum hanger for Projector", "VGA CABLE to connect e-learning server with Projector (properly trunked) MINIMUM OF 10 METERS",
                "5m CAT6e ETHERNET NETWORK CABLE (TO connect e-learning server to LAN), PROPERLY TRUNCKED", "Easy to use, user guide (HARD & SOFT COPY) with step by step tutorial GUIDELINES",
                "Provide a STANDALONE based learning management system (LMS)", "MUST COME WITH BUILD-IN TEXT CONTENTS THAT ARE WRITTEN IN LINE WITH THE CURRICULUM AS STIPULATED BY THE NERDC", "Installation & Integration OF HARDWARE AND SOFTWARE",
                "Training of two(2) CENTRE STAFF persons (Name & phone should be provided)"
            ],
            secondSection: [],
            error: []
        }
    },
    created() {
        axios.get(`http://lit-shore-55440.herokuapp.com/api/reports/${this.$route.params.id}`)
        .then(res => {
            this.answer = res.data.reports
            this.secondSection = res.data.reports.projectData.secondSection
        })
        .catch(err => this.error.push(err.response.data.error))
    },
    methods: {
        approveReport(){
            // http://lit-shore-55440.herokuapp.com/api/
            axios.put(`http://localhost:5000/api/report/${this.$route.params.id}/approve`)
            .then(res => {
                if(res.data.success){
                    this.msg = res.data.msg
                }
            }).catch(err => this.error.push(err))
        },
        rejectReport(){
            axios.put(`http://localhost:5000/api/report/${this.$route.params.id}/disapprove`)
            .then(res => {
                if(res.data.success){
                    this.msg = res.data.msg
                }
            }).catch(err => this.error.push(err))
        }
    }
}
</script>

<style scoped>
    .report-info {
        margin: 20px;
        /* padding: 20px;   */
        background: #fff;
        border-radius: 10px;
        max-height: 85vh;
        overflow: auto;
        position: relative;
    }

    .info {
        margin-bottom: 20px;
        border-bottom: 1px solid #e6e6e6;
        padding: 10px;
    }

    .label {
        font-weight: bold;
        color: green;
    }

    .btn-approve {
        background-color: #005c00;
        color: #fff;
        font-weight: bold;
        position: absolute;
        top: 10px;
        right: 10px;
   }

   .btn-approve:hover {
       background-color: green;
   }

   .btn-disapprove {
        background-color: #d03b00;
        color: #fff;
        font-weight: bold;
        position: absolute;
        top: 10px;
        right: 10px;
   }

   .top {
        text-align: right;
   }

   .available {
       font-weight: bold;
   }

   .second-section .label {
       margin-bottom: 10px;
   }

   .value b {
       font-size: 14px;
   }

   .msg {
       background-color: green;
       color: #fff;
       font-weight: bold;
       font-size: 15px;
       padding: 10px;
   }
</style>