<template>
    <div class="settings">
        <Header/>
        <div class="display">
            <div class="request-display">
                <div class="header">
                    <h3>Requests</h3>
                </div>
                <div class="requests" v-for="request in requests" :key="request._id">
                    <router-link to="#">{{ request.email }}</router-link>
                    <div class="btns">
                        <button class="approve" @click="approveUser(request.email)">Approve</button>
                        <button class="reject" @click="rejectUser(request._id)">Reject</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Header from '../../../components/Header.vue'
import axios from 'axios'

export default {
    components: {
        Header
    },
    data(){
        return {
            requests: [],
            error: ""
        }
    },
    created(){
        axios.get('http://lit-shore-55440.herokuapp.com/api/users')
        .then(res => this.requests = res.data.users)
        .catch(err => this.errors.push(err.response.data.err))
    },
    methods: {
        approveUser(email){
            axios.put('http://lit-shore-55440.herokuapp.com/api/user/approve', {
                email
            })
            .then(() => {
                axios.get('http://lit-shore-55440.herokuapp.com/api/users')
                .then(res => this.requests = res.data.users)
                .catch(err => this.error = err.response.data.err)
            })
            .catch(err => {
                this.errors.push(err)
            })
        },
        rejectUser(id){
            axios.delete(`http://lit-shore-55440.herokuapp.com/api/user/${id}/delete`)
            .then(() => {
                axios.get('http://lit-shore-55440.herokuapp.com/api/users')
                .then(res => this.requests = res.data.users)
                .catch(err => this.error = err.response.data.err) 
            })
            .catch((err) => this.errors.push(err))
        }
    }
}
</script>

<style scoped>
    .display {
        margin: 20px;
    }

    .header {
        padding: 15px;
        box-shadow: 1px 1px 6px 2px #ccc;
        text-transform: uppercase;
        margin-bottom: 10px;
        border-top-right-radius: 10px;
        border-top-left-radius: 10px;
        overflow: hidden;
    }
    
    .request-display {
        background: #fff;
        height: 80vh;
        width: 100%;
    }

    .requests {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 10px;
        border-bottom: 1px solid #f3f3f3;
    }

    .request-display a {
        font-size: 16px;
        text-decoration: none;
        color: #000;
    }

    .requests a:hover {
        color: green;
    }

    .request-display button {
        padding: 10px;
        margin-right: 5px;
        border: none;
        font-size: 15px;
        font-weight: bold;
        cursor: pointer;
        border-radius: 5px;
    }

    .requests .reject {
        background: #c50000;
        color: #fff;
    }

    .requests .approve {
        background: green;
        color: #fff;
    }

    .approve:hover {
        background-color: #298829;
    }

    .reject:hover {
        background-color: #ff4e4e;
    }

    @media(max-width: 769px) {
        .requests button {
            font-size: 13px;
            padding: 6px;
        }

        .requests a {
            font-size: 14px;
        }
    }
</style>